import React, { useState, useRef, useEffect } from 'react';
// import './App.css';

const App = () => {
    const [isCameraActive, setIsCameraActive] = useState(false);
    const videoRef = useRef(null);
    const [cameraDeviceId, setCameraDeviceId] = useState('');

    useEffect(() => {
        if (isCameraActive) {
            // 
            navigator.mediaDevices.enumerateDevices()
                .then(devices => {
                    // 
                    const videoDevices = devices.filter(device => device.kind === 'videoinput');

                    //  'environment' facingMode 
                    const mainCamera = videoDevices.find(device => device.label.toLowerCase().includes('back') || device.label.toLowerCase().includes('main'));

                    if (mainCamera) {
                        setCameraDeviceId(mainCamera.deviceId);

                        // 
                        navigator.mediaDevices.getUserMedia({
                            video: {
                                deviceId: mainCamera.deviceId
                            }
                        })
                            .then((stream) => {
                                //  video 
                                if (videoRef.current) {
                                    videoRef.current.srcObject = stream;
                                }
                            })
                            .catch((err) => {
                                console.log("Error accessing camera: ", err);
                            });
                    } else {
                        console.log("No main camera found");
                    }
                })
                .catch(err => {
                    console.log("Error enumerating devices: ", err);
                });
        } else {
            // 
            if (videoRef.current) {
                let stream = videoRef.current.srcObject;
                let tracks = stream?.getTracks();
                tracks?.forEach(track => track.stop());
                videoRef.current.srcObject = null;
            }
        }
    }, [isCameraActive]);

    const toggleCamera = () => {
        setIsCameraActive(!isCameraActive);
    };

    return (
        <div className="App">
            <h1></h1>
            <div>
                <video ref={videoRef} width="100%" height="auto" autoPlay></video>
            </div>
            <button onClick={toggleCamera}>
                {isCameraActive ? '' : ''}
            </button>
        </div>
    );
}

export default App;
